div.home-container {
    padding-top: 200px;
    margin-top: 100px;
  }
  
  section#home h1 {
    font-size: 36px;
  }
  
  section#home h2 {
    font-size: 36px;
    margin-bottom: 20px;
    -webkit-animation-delay: 0.5s;
            animation-delay: 0.5s;
  }
  
  section#home a {
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
  }
  
  section#home button {
    background: rgba(204, 204, 204, 0);
    color: midnightblue;
    border: 2px solid midnightblue;
  }
    
  section#home button:hover {
    background-color: midnightblue;
    color: azure;
  }
  
  section#home i {
    margin-left: 10px;
  }
  
  span.name {
    color: midnightblue;
    font-weight: bold ;
    font-size: 48px;
  }
section#about img {
    width: 70%;
    height: auto;
    box-shadow: 4px 8px midnightblue;
    margin-bottom: 30px;
    position: relative;
    left: 70px;
    border-radius: 10px;
  }
  
  section#about div.p-column {
    padding: 8px;
    text-align: justify;
  }
  
  div.icon-row {
    margin-bottom: 50px;
  }
  
  div.circle {
    background-color: midnightblue;
    color: white;
    border-radius: 50%;
    width: 100px;
    height: 100px;
    text-align: center;
    padding-top: 10px;
    box-shadow: 2px 4px #24a0ed;
    margin: 25px;
  }
  
  h2.tech {
    font-size: 20px;
    position: relative;
    left: 54px;
  }
  
  h2.business {
    font-size: 20px;
    position: relative;
    left: 39px;
  }
  
  h2.retail {
    font-size: 20px;
    position: relative;
    left: 50px;
  }
div.item-container {
    position: relative;
    width: 100%;
    margin-bottom: 40px;
    padding: 0;
  }
  
  img {
    opacity: 1;
    display: flex;
    align-items: center;
    width: 100%;
    height: 200px;
    transition: .5s ease;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    box-shadow: 2px 4px midnightblue;
    border-radius: 10px;
  }
  
  div.middle {
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    text-align: center;
    margin-top: 40%;
  }
  
  div.middle p.text-name {
    font-size: 16px;
    margin: 10px;
  }
  
  div.middle p.text-tech {
    font-size: 14px;
    color: midnightblue;
    font-weight: bold;
  }
  
  div.middle button {
    margin: 10px;
    background-color: rgba(204, 204, 204, 0);
    color: black;
    border: 2px solid black;
  }
  
  div.middle button:hover {
    background-color: #191970;
    color: white;
    border-color: black;
  }
  
  div.item-container:hover img {
    opacity: 0.1;
  }
  
  div.item-container:hover div.middle {
    opacity: 1;
  }
